* {
    box-sizing: border-box;
  }
  
  body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
      Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #fafafa;
    color: #212121;
    line-height: 1.5;
    padding-left: 15px;
  }
  
  #search-box {
    font: inherit;
    padding: 8px;
    max-width: 380px;
    min-width: 300px;
    margin-bottom: 16px;
    margin-top: 16px;
  }
  
   /* renderList */
  
   ul.country-list {
     list-style-type: none;
     font-size: 21px;
     padding: 0;
    }
    li.list__item {
      display: flex;
      align-items: center;
    }
  
   /* img.list__flag {} */
  
   p.list__name {
     /* color: red; */
     padding-left: 15px;
     margin-top: 10px;
     margin-bottom: 10px;
   }
   
   /* renderInfo */
  
   /* div.country-info {}
   img.info__flag {} */
   span.info__name {
     font-size: 45px;
     padding-left: 15px;
   }
  
   p.info__data {
     font-size: 21px;
  
   }